.corporate {
    overflow: hidden;
    .page__directions {
        margin-bottom: 79px;
        .title {
            font-size: 30px;
            margin-bottom: 0;
        }
    }
    .page__header {
        padding-bottom: 60px;
    }

    &__body {
        background: #ff004c;
        padding-bottom: 100px;
        position: relative;

        &:after {
            content: "";
            position: absolute;
            bottom: -149px;
            left: 0;
            width: 110%;
            height: 222px;
            transform: skew(-4deg,5deg);
            background: #ff004c;
            border-bottom: 10px solid #000;
        }
        .title {
            max-width: 1210px;
        }
    }
    &__gallery {
        display: flex;
        flex-wrap: wrap;
        position: relative;
        max-width: 76%;
        margin: 0 auto;
        padding-top: 68px;
        padding: 68px 10% 0;
        z-index: 1;
        &:before {
            content: "";
            position: absolute;
            z-index: -2;
        }
        &:after {
            content: "";
            position: absolute;
            z-index: -1;
        }
        &:before {
            top: 1%;
            left: 6%;
            width: 85%;
            height: 109%;
            transform: skew(5deg,5deg);
            background: linear-gradient(55deg, $yellow 50%, rgba(255, 255, 255, 0) 50% );
            border-left: 10px solid #000;
            border-bottom: 10px solid #000;
        }
        &:after {
            top: -2%;
            left: -1%;
            width: 100%;
            height: 114%;
            transform: skew(-2deg,1deg);
            background-color: $yellow;
            background: linear-gradient(52deg, rgba(255, 255, 255, 0) 50%, $yellow 50%);
            border-right: 10px solid #000;
            border-top: 10px solid #000;
        }  
    }
    &__item {
        text-align: center;
        &-wrap {
            width: 50%;
            margin-bottom: 73px;
            &:nth-child(2n+1) {
                position: relative;
                top: -50px;
            }
            &:nth-child(2n) {
                position: relative;
                top: 45px;
            }
        }
    }
    &__title {
        font-size: 18px;
        font-weight: 700;
    }
    &__img {
        width: 100%;
        height: 100%;
        overflow: hidden;
        background-size: cover;
        background-position: center;
        &-wrap {
            position: relative;
            margin: 0 auto;
            width: 283px;
            height: 163px;
            z-index: 1;
            &:after {
                content: "";
                position: absolute;
                top: 5%;
                left: 4%;
                width: 100%;
                height: 100%;
                background: url(../assets/img/dots.jpg);
                z-index: -1;
            }
        }
    }
    &__header {
        padding-top: 130px;
        padding-bottom: 0;
        overflow: hidden;
        z-index: -1;
        &:before { 
            background-image: url("../assets/img/bgqa-fon.png");
            background-position: bottom -100px center;
            background-position: bottom 95% center;
            background-size: 126vw;
            pointer-events: none;
        }
        .wrap {
            background-image: url("../assets/img/bgqa-wrap.png");
            background-repeat: no-repeat;
            min-height: 46vw;
            background-size: 82vw;
        }
        p {
            padding: 15vw 27vw 1vw 26vw;
            font-size: 2.2vw;
            font-weight: 800;
            text-align: left;
            text-transform: uppercase;
        }
    }
    &__qa {
        position: relative;
        background: #ff004c;
        padding-top: 49px;
        padding-bottom: 133px;
        &:after {
            content: "";
            position: absolute;
            top: -50px;
            left: 0;
            width: 110%;
            height: 87px;
            transform: skew(0deg,2deg);
            background: #ff004c;
            border-top: 10px solid #000;
        }
        .title {
            text-transform: initial;
            max-width: 890px;
            margin: 0 auto 46px;
        }
        .kviz-btn {
            max-width: 427px;
            a {
                font-size: 39px;
            }
        }
    }
    &__how {
        background: #ff004c;
        padding-bottom: 0;
        position: relative;
        &:after {
            content: "";
            position: absolute;
            bottom: -56px;
            left: 0;
            width: 110%;
            height: 130px;
            transform: skew(-4deg,3deg);
            background: #ff004c;
            border-bottom: 10px solid #000;
        }
        .partners__how {
            padding-top: 25px;
        }
    }
    &__partners {
        background: #14a1cc;
        padding-top: 103px;
        padding-bottom: 42px;
        &-slider {
            display: flex;
            background: $yellow;
            border: 10px solid #000;
            max-width: 1052px;
            margin: 0 auto 127px;
            padding: 20px 0;
            img {
                max-width: 160px;
                margin: 0 auto;
            }
        }
        .title {
            margin-bottom: 20px;
        }
        p {
            color: #fff;
            text-align: center;
            font-size: 36px;
            font-weight: 700;
            max-width: 1013px;
            margin: 0 auto 40px;
        }
        .arrow-prev {
            left: -40px;
        }
        .arrow-next {
            right: -51px;
        }
        .kviz-btn {
            max-width: 427px;
            a {
                font-size: 39px;
            }
        }
    }
}